.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
}

.list {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.item {
  width: 250px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;

  & *:not(svg) {
    margin: 0 !important;
    font-size: unset !important;
    font-size: 12px !important;
    color: white !important;
  }
}
